<template>
  <v-container fluid class="down-top-padding">
    <v-row>
         <v-col cols="12"><h3 class="text-center">{{$t('header.insertData')}}</h3></v-col>
      <v-col :cols="12" :sm="6" :md="item.cols" :lg="item.md" v-for="(item, index) in insertData" :key="index">
       <div @click="setDialog(item.click)"  class="card_style bg-white">
         <v-row>
           <v-col :class="item.iconClass" :cols="item.iconCol"><img width="50" height="50" :src="item.icon"/></v-col>
           <v-col :cols="item.textCol"> {{$t(item.name)}}</v-col>
         </v-row>
       </div>
      </v-col>
    </v-row>
      <v-row>
        <v-col cols="12"><h3 class="text-center">{{$t('header.listData')}}</h3></v-col>
      <v-col :cols="12" :sm="6" :md="item.cols" :lg="item.md" v-for="(item, index) in listData" :key="index">
       <div @click="setDialog(item.click)" class="card_style">
         <v-row>
           <v-col :class="item.iconClass" :cols="item.iconCol"><img width="50" height="50" :src="item.icon"/></v-col>
           <v-col :cols="item.textCol"> {{$t(item.name)}}</v-col>
         </v-row>
       </div>
      </v-col>
    </v-row>
    <component :is="componentData" :dialog="dialog"  @dialog="dialog=$event" />
  </v-container>
</template>


<script>

import {insertData,listData} from './dashboardItems'
import insertMonthlyNews from '../../components/dialogs/insertMonthlyNewsDialogs'
import insertEvent from '../../components/dialogs/insertEventDialogs'
import insertFood from '../../components/dialogs/insertFoodDialog'
import insertAnnoucment from '../../components/dialogs/insertAnnocumentDialogs'
import insertLessonProgram from '../../components/dialogs/insertLessonDialogs'
export default {
  name: "BasicDashboard",
  components:{
    insertMonthlyNews,insertEvent,insertFood,insertAnnoucment,insertLessonProgram
  },
  data: () => ({
    insertData: insertData,
    listData: listData,
    componentData:"",
    dialog:false
  }),
  watch:{
    dialog(data){
      if(!data){
        this.componentData=""
      }
    }
  },
  methods: {
    setDialog(func){
     let type = func()
    if(type=='insertTeacher'){
      this.dialog==false
      this.$router.push('/pages/insert-teacher')

    }
    else if(type=='insertStudent'){
      this.dialog==false
      this.$router.push('/pages/insert-parent')
    }
    else if(type=='listStudent'){
      this.dialog==false
      this.$router.push('/pages/student-list')
    }
    else if(type=='listClass'){
      this.dialog==false
      this.$router.push('/pages/class-list')
    }
    else if(type=='messages'){
      this.dialog==false
      this.$router.push('/pages/messages')
    }
    else if(type=='listMonthlyNews'){
      this.dialog==false
      this.$router.push('/pages/monthly-news')
    }
    else if(type=='listEvent'){
      this.dialog==false
      this.$router.push('/pages/event-list')
    }
    else if(type=='listFood'){
      this.dialog==false
      this.$router.push('/pages/food-list')
    }
    else if(type=='listAnnoucment'){
      this.dialog==false
      this.$router.push('/pages/annoucment-list')
    }
    else if(type=='insertLesson'){
      this.dialog==false
      this.$router.push('/pages/lessons')
    }
    else if(type=='insertMonthlyNews' || type=='insertEvent'|| type=='insertFood'||type=='insertAnnoucment'||type=='insertLessonProgram'){
      this.dialog=true
      this.componentData=type
    }
    },
  }
};
</script>

