<template>
   <v-row justify="center">
    <v-dialog v-model="dialog" persistent  max-width="800px">
      <v-card>
        <v-card-title>
          <span class="headline"> <v-icon color="primary">mdi-calendar-multiple-check</v-icon> Ders Programı Ekle</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
                <v-col cols="12" sm="6">
      <v-date-picker v-model="date" range></v-date-picker>
    </v-col>
    <v-col cols="12" sm="6">
        <v-text-field :error-messages="errDate"  persistent-hint v-model="dateRangeText"  label="Seçilen Tarih" hint="Tarihi sol taraftaki takvimden seçiniz." readonly></v-text-field>
   <div class="mt-2">
     <h4 style="margin-top:20px">Gönderi Tipi : </h4>
      <v-radio-group v-model="food.isWeekly" :mandatory="false" row>
      <v-radio label="Haftalık Gönderi" :value="1"></v-radio>
      <v-radio label="Aylık Gönderi" :value="0"></v-radio>
    </v-radio-group>
   </div>

    </v-col>
    <v-col cols="12">

    </v-col>
    <v-col cols="12">
        <el-upload
  class="upload-demo"
  drag
  :action="'#'"
  :on-change="fileUpload"
  :multiple="false"
  :limit="1"
  :auto-upload="false">
  <i class="el-icon-upload"></i>
  <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
  <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 5MB
    <p style="color:red"> {{errMsg}}</p>
  </div>
  <div style="color:red"></div>
</el-upload>

    </v-col>
            </v-row>
          </v-container>
        </v-card-text>
         <v-card-actions>
          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn :loading="loading" :disabled="loading" class="ma-2" color="primary" @click="sendFood()" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <snack-bar :snackbar="snackbar" @finished="snackbar=$event"></snack-bar>
  </v-row>
</template>

<script>
export default {
props:{
      dialog:{
        default:false,
        type : Boolean,
        required:true
    },
},
computed:{
dateRangeText () {
        return this.date.join(' ~ ')
      },
},
watch:{
  date(){
      this.food.startDate = this.date[0]
  this.food.endDate = this.date[1]
  }
},
methods:{
 async sendFood(){
   this.loading = true
   if(this.food.startDate==''||this.food.endDate===undefined||this.food.endDate==''||this.food.file.uid===undefined){
      if(this.food.startDate==''){
        this.errDate.push('Başlangıç Tarihini Seçiniz')
      }
      if(this.food.endDate==''){
        this.errDate.push('Bitiş Tarihini Seçiniz')
      }
       if(this.food.endDate===undefined){
        this.errDate.push('Bitiş Tarihini Seçiniz')
      }
      if(this.food.file.uid===undefined){
        this.errMsg = 'Dosya seçiniz.'
      }
        this.loading = false
   }
   else{
      let fd = new FormData()
    fd.append('startDate',this.food.startDate)
    fd.append('endDate',this.food.endDate)
    fd.append('isWeekly',this.food.isWeekly)
    fd.append('image',this.food.file)
    let result = await this.$api.postfd('foodList','file',fd)
    if(result.status=='success'){
      this.$api.tnotify(`${this.food.isWeekly==1?'Haftalık Yeni Yemek Programı':'Aylık Yeni Yemek Programı'}`,this.food.startDate+' ile '+ this.food.endDate + ' tarihleri arası Yeni Yemek Programı Eklendi!','teachers')
      this.$api.tnotify(`${this.food.isWeekly==1?'Haftalık Yeni Yemek Programı':'Aylık Yeni Yemek Programı'}`,this.food.startDate+' ile '+ this.food.endDate + ' tarihleri arası Yeni Yemek Programı Eklendi!','parents')
      let type = 'jpg'
      if(this.food.file.type.includes('pdf'))
        type='pdf'
      let path = result.path.replace('public/',this.$api.BASE_API_URL)
      this.$emit('setNewFoodList',{startDate: this.food.startDate, endDate: this.food.endDate, isWeekly : this.food.isWeekly, file:path ,type:type})
         this.loading = false
         this.snackbar=true
      this.closeModal()
    }
    else{
         this.loading = false
      alert('hata')
    }
   }
  },
    fileUpload(e){
      if(e.raw.type=='application/pdf' || e.raw.type=='image/jpeg' || e.raw.type=='image/png'){
          if(e.size>5000000){
       this.errMsg = "Dosya Boyutunuz 5MB üzerinde lütfen " +e.raw.type +' dosyanızı kaldırın. Dosya Boyutu : ' +e.raw.size+' bayt'
      }
      else{
        this.errMsg=""
       this.food.file = e.raw;
      }
      }
      else{
          this.errMsg="Dosya formatınız .pdf, .jpg, .jpeg veya .png olmalıdır."
      }
    },
    closeModal(){
        this.dialog=false
        this.$emit('dialog',false)
        this.food={
            startDate:"",
            endDate:"",
            file:[],
            isWeekly:0,
            dataURL:""
        }
        this.date=[]
    }
},
data(){
    return{
      errDate:[],
       loading:false,
        date:[],
        errMsg:"",
        snackbar:false,
        food:{
            startDate:"",
            endDate:"",
            file:[],
            isWeekly:0,
            dataURL:""
        }
    }
}
}
</script>

<style>
@media only screen and (max-width: 900px) {
  .v-dialog {
    margin : 0px !important
  }
}
.el-upload, .el-upload-dragger {
    width: 100% !important;
}
</style>